body {
  font-family: 'Open Sans', sans-serif;
  position: fixed;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

/*===================COMMON START====================*/
#root {
  width: 100vw;
  height: 100vh;
  background: #7ddd8e;
  background: -webkit-linear-gradient(135deg, #7ddd8e, #2956b1, #792484);
  background: -webkit-linear-gradient(315deg, #7ddd8e, #2956b1, #792484);
  background: linear-gradient(135deg, #7ddd8e, #2956b1, #792484);
  -webkit-transition: width 0.75s, height 0.75s, -webkit-transform 0.75s; /* Safari */
  transition: width 0.75s, height 0.75s, -webkit-transform 0.75s;
  transition: width 0.75s, height 0.75s, transform 0.75s;
  transition: width 0.75s, height 0.75s, transform 0.75s, -webkit-transform 0.75s;
  overflow: auto;
}

.Toastify__toast--success {
  background: #47c75e;
  color: #ffffff;
}

.Toastify__toast {
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  letter-spacing: 2px;
  padding-left: 25px;
  font-size: 1.5rem;
}

.Toastify__toast-body {
  z-index: 9999;
}

.Toastify__progress-bar {
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9998;
}
/*===================COMMON END====================*/

/*===================TOPBAR START====================*/
.navbar-brand {
  padding: 3px 15px;
}

.navbar{
  border-radius: 0px;
  background-image: none;
  border:none;
  background: transparent;
}

.navbar-list > a {
    color: #ffffff !important;
    text-transform: uppercase;
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: bold;
    letter-spacing: 2px;
}

.navbar-list > a:hover {
  color: #000 !important;
}

/*===================TOPBAR END====================*/

/*===================LOGIN START====================*/
.login-container {
  height: 100vh;
  width: 100vw;
  padding-top: 15vh;
  background: #2d7e4b;  /* fallback for old browsers */
  background: -webkit-linear-gradient(135deg, #83b0e9, #2d7e4b);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(315deg, #83b0e9, #2d7e4b);
  background: linear-gradient(135deg, #83b0e9, #2d7e4b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.login-container>div>div>div {
  max-width: 350px;
}

.login-logo {
  width: 70%;
  margin: 0px auto;
  margin-bottom: 50px;
}

.login-form-group {
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.4rem;
  letter-spacing: 1px;
}

.login-form-group > input {
  background-color: transparent;
  padding: 0px;
  border-radius: 0px;
  border: 0px;
  box-shadow: none;
  margin-bottom: 25px;
  font-size: 2rem;
  color: #333333;
  border-bottom: 2px solid #333333;
}

.login-form-group > .control-label {
  margin-bottom: 0px;
  color: #aeff77;
  letter-spacing: 2px;
}

.login-form-group > button {
  width: 100%;
  padding: 12px;
  border-radius: 0px;
  border: 0px;
  color:#ffffff;
  font-size: 1.4rem;
  background-color: #333333;
  background-image: none;
  letter-spacing: 1px;
  font-weight: bold;
}

.login-form-group > button:hover {
  background-color: #b2e88d;
  background-image: none;
  color: #000000;
}

.login-form-group > input:hover {
  box-shadow: none;
  border-color: #b2e88d;
}

.login-error {
    background-color: #c0392b;
    padding: 4px 8px;
    margin-top: 10px;
    text-align: center;
}

.login-error > span {
  color: #fff;
  font-size: 1.5rem;
}

/*===================LOGIN START====================*/

/*===================COMMON START====================*/
.record-panel {
  background-color: rgba(51, 51, 51, 0.7);
  padding: 10px;
  margin: 5px;
  border-radius: 2px;
}

.record-container {
  display: flex;
  height: 80vh;
  flex-wrap: wrap;
}

.record-panel>h1 {
  font-size: 1.7rem;
  font-family: 'Roboto Condensed', sans-serif;
  color: rgba(255, 255, 255, 0.4);
  margin-top: 5px;
  margin-bottom: 15px;
  font-weight: bold;
  letter-spacing: 1px;
  padding-left: 5px;
}

.record-table {
  height: 70vh;
  overflow-x: hidden;
  overflow-y: auto;
  color: #ffffff;
}

.table-hover>tbody>tr:hover {
  background-color: rgba(27, 27, 27, 0.3) !important;
}

.hidden {
  display:none;
}

/*===================COMMON END====================*/
/*===================RECORDS FILTER START====================*/
.record-filter-container {
  text-align: center;
}

.record-filter-text {
  color: #ffffff;
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  letter-spacing: 2px;
  font-size: 1.7rem;
}

/*===================RECORDS FILTER END====================*/
/*===================EXPENSES PANEL START====================*/
.expenses-panel {
  flex: 1.5 1;
}

/*===================EXPENSES PANEL END====================*/
/*===================ACTIVEINCOME PANEL START====================*/
.active-panel {
  flex: 1 1;
}

.active-panel > h1 > .glyphicon-plus {
  cursor: pointer;
}

/*===================ACTIVEINCOME PANEL END====================*/
/*===================PASSIVEINCOME PANEL START====================*/
.passive-panel {
  flex: 1 1;
}

.passive-panel > h1 > .glyphicon-plus {
  cursor: pointer;
}

/*===================PASSIVEINCOME PANEL END====================*/

/*===================MODAL START====================*/
.modal-header {
  border-bottom: 0px;
  text-align: center;
  padding-bottom: 5px;
}

.modal-header > h4 {
  font-size: 2.8rem;
  letter-spacing: 2px;
  color: #000000;
  font-weight: bold;
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
}

.modal-subtitle {
  letter-spacing: 1px;
  font-style: italic;
}

.modal-body {
  padding: 20px 30px;
}

.modal-footer {
  border-top: 0px;
  padding: 0px;
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  color: #ffffff;
}

.modal-footer:hover {
  cursor: pointer;
}

.modal-content {
  border-radius: 0px;
  background-color: #ffffff;
  color: #666666;
}

.modal-dialog {
  margin: 20vh auto;
}

.record-input {
  background-color: transparent;
  padding: 0px;
  border-radius: 0px;
  border: 0px;
  box-shadow: none;
  margin-bottom: 20px;
  font-size: 1.5rem;
  color: #333333;
  border-bottom: 2px solid #333333;
  cursor: text;
  height: 25px;
}

.record-input:focus {
  border-color: transparent;
  box-shadow: none;
  border-bottom: 2px solid #333333;
}

#record-modal-submit {
  width: 50%;
  float:left;
  padding: 15px;
  background-color: #7ddd8e;
  letter-spacing: 2px;
  font-size: 2rem;
}

#record-modal-close {
  float:left;
  width: 50%;
  background-color: #5d8bb1;
  padding: 15px;
  letter-spacing: 2px;
  font-size: 2rem;
}

#record-modal-submit:hover {
  background-color: #47c75e;
}

#record-modal-close:hover {
  background-color: #3187ce;
}

.record-toastrContainer {
  z-index: 1040 !important;
  position: absolute;
  bottom: 50px;
  right: 50px;
}
/*===================MODAL END====================*/

/* COMMON STYLES */
.Add-Container {
    height: 100vh;
    background-color: #ffffff;
    padding: 15px;
    padding-top: 25px;
}

.add-row {
    font-family: 'Roboto Condensed', sans-serif;
    margin-right: 0px;
    margin-left: 0px;
}

.btn {
    width: 100%;
}

.form-control {
    height: 100%;
    text-align: center;
    font-weight: bold;
    border: none;
    background-color: transparent;
    font-size: 2em;
    border-radius: 0px;
    box-shadow: none;
}

.form-control:hover {
    text-align: center;
    border: none;
    background-color: transparent;
}

.form-control:focus {
    box-shadow: none;
    border-bottom: 2px solid green;
}


/* TITLE STYLES */
.add-row-title {
    height: 6vh;
    text-align: center;
}

.add-row-title > h1 {
    margin: 0px;
    font-weight: bold;
}

/* MAIN STYLES */
.add-row-mainRow {
    height: 15vh;
    background-color: #27ae60;
    color: #ffffff;
    border-radius: 5px;
    margin: 0px 5px 15px 5px;
}

.add-row-mainRow > .col-xs-9 {
    height: 100%;
    text-align: right;
}

#transaction-amt{
    font-weight: bold;
    font-family: 'Open Sans', sans-serif;
    font-size: 4em;
    line-height: 3em;
}

/* DESCRIPTION + TIMESTAMPE STYLES */
.add-row-form {
    height: 7vh;
}

/* CATEGORIES STYLES */
.add-row-category {
    height: 7vh;
}

.add-row-category > .col-xs-4 {
    padding: 5px 5px 0px 5px;
    height: 100%;
}


.btn-active,
.btn-active:hover,
.btn-active:active,
.btn-active:focus {
    color: #fff !important;
    background-color: #27ae60 !important;
    border-color: #27ae60 !important;
    box-shadow: none !important;
}

.btn-outline-secondary {
    color: #27ae60;
    background-color: transparent;
    background-image: none;
    border: 2px solid #27ae60;
    border-radius: 5px;
    font-size: 1.4em;
}

/* CALCULATOR STYLES */

.add-row-calculator {
    height: 11vh;
    text-align: center;
}

.add-row-calculator > div {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 2.5em;
    line-height: 82px;
    margin: 0px;
    font-weight: bold;
}

.add-row-templates {
    height: 30vh;
    overflow-y: scroll;
    margin-top: 20px;
}

.template-box {
    width: 100%;
    text-align: center;
    height: 50px;
    border-radius: 5px;
    border: 3px solid #27ae60;
    margin-bottom: 10px;
    font-size: 1.5em;
    line-height: 2;
    color: #27ae60;
}
